import React from 'react';
import { Typography, Form, Input, Button, message } from 'antd';

const { Title, Paragraph } = Typography;

function Contact() {
  const [form] = Form.useForm();

  const onFinish = (values) => {
    console.log(':', values);
    message.success('');
    form.resetFields();
  };

  return (
    <div style={{ padding: '50px 0' }}>
      <Title level={2}></Title>
      <Paragraph>
        xxxxxx@xxx.com<br />
        xxxxxx<br />
        LinkedIn<a href="https://linkedin.com/in/xxxxxx" target="_blank" rel="noopener noreferrer">xxxxxx</a>
      </Paragraph>
      <Title level={3}></Title>
      <Form form={form} onFinish={onFinish} layout="vertical">
        <Form.Item name="name" label="" rules={[{ required: true, message: '' }]}>
          <Input />
        </Form.Item>
        <Form.Item name="email" label="" rules={[{ required: true, message: '' }]}>
          <Input />
        </Form.Item>
        <Form.Item name="message" label="" rules={[{ required: true, message: '' }]}>
          <Input.TextArea rows={4} />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit"></Button>
        </Form.Item>
      </Form>
    </div>
  );
}

export default Contact; 